<script>



export default {

    props:[
        'Items'
    ],

    data() {
        return {
        ProductImage:this.Items[0].colors[1].url,
        ProductName: this.Items[0].Name,
        ProductPrice:  this.Items[0].price,
        ProductDetails:this.Items[0].Details,
        ProductColor:  this.Items[0].colors,
        ProductSizes:  this.Items[0].sizes,
        MainIndex : 0,
        sizeIndex:null,

        }
    },
    methods: {
        showItem(index){
            const wraper = document.querySelector('.sliderWraper');
            wraper.style.transform = `translateX(${-100 * index}vw)`

            this.ProductImage = this.Items[index].ImageUrl
            this.ProductName = this.Items[index].Name
            this.ProductPrice = this.Items[index].price
            this.ProductDetails = this.Items[index].Details
            this.ProductColor = this.Items[index].colors
            this.ProductSizes = this.Items[index].sizes

            this.MainIndex = index
        },
        clickColors(index){
                this.ProductImage = this.Items[this.MainIndex].colors[index].url
 
        },
        clickSize(index){
            const sizek = document.querySelectorAll('.size')

          //  for(let x=0; x<sizek.length; x++){
            // sizek[x].style.color = 'black'
           // }

            sizek.forEach((i)=>{
                i.style.backgroundColor = 'white'
                i.style.color = 'black'
            })


            sizek[index].style.backgroundColor = 'black'
            sizek[index].style.color = 'white'

            this.sizeIndex = index
        },
        buyNow(){
            this.$emit('clickBuy')
        }
    },

}
</script>

